/* core */
import React from 'react';
import { connect } from 'react-redux';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* actions */
import * as layoutActions from '@src/states/layout/actions';

/*
	form
*/
class PageComponent extends React.Component {

	render() {

		/* 
			raw data
			this format is only assumed - dummy data
			will have to redo this logic to filter the actual format
		*/
		var aps = [
			{
				name:"XAP-1510 87:10",
				mac: "A4:13:4E:37:87:10",
				ip: "192.168.0.10",
				software: "XAP-1510-Z160920-1558",
				status: "offline"
			},
			{
				name:"XYZ-1510 23:04",
				mac: "C8:26:9R:35:23:04",
				ip: "192.168.0.10",
				software: "XAP-1510-Z160920-1558",
				status: "pending"
			},
			{
				name:"ABC-1510 23:11",
				mac: "B3:75:2G:23:23:11",
				ip: "192.168.0.10",
				software: "XAP-1510-Z160920-1558",
				status: "online"
			},
			{
				name:"XYZ-1510 23:04",
				mac: "C8:26:9R:35:23:04",
				ip: "192.168.0.10",
				software: "XAP-1510-Z160920-1558",
				status: "pending"
			},
			{
				name:"XAP-1510 87:10",
				mac: "A4:13:4E:37:87:10",
				ip: "192.168.0.10",
				software: "XAP-1510-Z160920-1558",
				status: "offline"
			}
		];
		var Rows = [];
		for (var i = 0; i < aps.length; i++) {
			var odd = (i+1)%2;
			Rows.push(
				<tr className={(odd?" odd":" even")}>
					<td className="bold">{aps[i].name}</td>
					<td className={"status-"+aps[i].status}>{aps[i].status}</td>
					<td>{aps[i].mac}</td>
					<td>{aps[i].ip}</td>
					<td>{aps[i].software}</td>
				</tr>
			);
		}

		return (
			<Layout router={this.props.router} route={this.props.route}>
				
				<Box box={{title:this.props.route.page.title}}>

					<div className="LuxulForm">

						<div className="formSection">
							<p>View the current status of each AP assigned to the Wireless Controller:</p>
						</div>

						<div className="LuxulRows">
							<div>

								<table>
									
									<tr className="even">
										<th>AP Name:</th>
										<th>Status:</th>
										<th>Mac Address:</th>
										<th>IP Address:</th>
										<th>Software Version:</th>
									</tr>

									{Rows}

								</table>

							</div>
						</div>

					</div>
				
				</Box>

			</Layout>
		)
	}

};


const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)(PageComponent);


