/* core */
import React from 'react';
import { connect } from 'react-redux';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* actions */
import * as layoutActions from '@src/states/layout/actions';

/*
	form
*/
class PageComponent extends React.Component {

	render() {

		/* 
			raw data
			this format is only assumed - dummy data
			will have to redo this logic to filter the actual format
		*/
		var aps = [
			{
				name:"XAP-1510 87:10",
				mac: "A4:13:4E:37:87:10",
				ip: "192.168.0.10",
				software: "XAP-1510-Z160920-1558",
				status: "offline"
			},
			{
				name:"XYZ-1510 23:04",
				mac: "C8:26:9R:35:23:04",
				ip: "192.168.0.10",
				software: "XAP-1510-Z160920-1558",
				status: "pending"
			},
			{
				name:"ABC-1510 23:11",
				mac: "B3:75:2G:23:23:11",
				ip: "192.168.0.10",
				software: "XAP-1510-Z160920-1558",
				status: "online"
			},
			{
				name:"XYZ-1510 23:04",
				mac: "C8:26:9R:35:23:04",
				ip: "192.168.0.10",
				software: "XAP-1510-Z160920-1558",
				status: "pending"
			},
			{
				name:"XAP-1510 87:10",
				mac: "A4:13:4E:37:87:10",
				ip: "192.168.0.10",
				software: "XAP-1510-Z160920-1558",
				status: "offline"
			}
		];
		var Aps = {};
		/*
			build <th>s
		*/
		Aps.ids = [<th></th>];
		Aps.names = [<th className="even">AP Name:</th>];
		Aps.macs = [<th className="even">Mac Address:</th>];
		Aps.ips = [<th className="even">IP Address:</th>];
		Aps.softwares = [<th className="even">Software Version:</th>];
		Aps.statuses = [<th className="even">Status:</th>];
		/*
			build <td>s
		*/
		for (var i = 0; i < aps.length; i++) {
			var odd = (i+1)%2;
			Aps.ids.push(<th>AP {i+1}:</th>);
			Aps.names.push(<td className={(odd?" odd":" even")}>{aps[i].name}</td>);
			Aps.macs.push(<td className={(odd?" odd":" even")}>{aps[i].mac}</td>);
			Aps.ips.push(<td className={(odd?" odd":" even")}>{aps[i].ip}</td>);
			Aps.softwares.push(<td className={(odd?" odd":" even")}>{aps[i].software}</td>);
			Aps.statuses.push(<td className={(odd?" odd":" even") + (" status-"+aps[i].status)}>{aps[i].status}</td>);
		}


		return (
			<Layout router={this.props.router} route={this.props.route}>
				
				<Box box={{title:this.props.route.page.title}}>

					<div className="LuxulForm">

						<div className="formSection">
							<p>View the current status of each Access Point assigned to the Wireless Controller:</p>
						</div>

						<div className="LuxulColumns">
							<div>

								<table>
									<tr className="bold green">
										{Aps.ids}
									</tr>
									<tr className="bold">
										{Aps.names}
									</tr>
									<tr>
										{Aps.macs}
									</tr>
									<tr>
										{Aps.ips}
									</tr>
									<tr>
										{Aps.softwares}
									</tr>
									<tr>
										{Aps.statuses}
									</tr>
								</table>

							</div>
						</div>

						<div className="formSection right">
							<label><sup><span className="">scroll to see more</span> <span className="fontIcon icon-carat_right alignWithText"></span></sup></label>
						</div>
						<div className="formSection right">
							<button className="primary small" style={{"margin-top":"1.75rem"}}><b>Add AP</b></button>
						</div>
						<div className="formSection right">
							<button className="primary small"><b>Manage APs</b></button>
						</div>

					</div>
				
				</Box>

			</Layout>
		)
	}

};


const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)(PageComponent);


